import React, { Component } from "react";
import { Table } from "react-bootstrap";
import { Button } from "bootstrap";
import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";

const Cart = () => {
  let history = useHistory();
  const [cartArray, setData] = useState([]);

  useEffect(() => {
    setData(history.location.state.cartData);
    console.log(cartArray);
    console.log(cartArray);
  }, []);

  const cancelfunc = (id, ky) => {
    // let index = cartArray.findIndex((item) => item.id === id);
    cartArray.splice(ky, 1);
    // cartArray.filter((item) => item.id !== id);
    setData([...cartArray]);
  };

  return (
    <div className="d-flex flex-column">
      <div className="d-flex flex-row">
        <button onClick={() => history.push("/", { cartData: cartArray })}>
          back
        </button>
        <h3 className="d-inline">Cart</h3>
      </div>

      <Table striped bordered hover variant="white" size="sm">
        <thead>
          <tr>
            <th>ID</th>
            <th>Subject</th>
            <th>Date</th>
            <th>Time</th>
            <th>Availability</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {cartArray?.map((item, ky) => {
            return (
              <tr>
                <td>{item.id}</td>
                <td>Python</td>

                <td>{item?.date?.toString()}</td>
                <td>{item?.time}</td>
                <td>{item?.seat}seats available</td>

                <td>
                  <button onClick={() => cancelfunc(item.id, ky)}>
                    cancel
                  </button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </Table>
    </div>
  );
};

export default Cart;
